.row {
	display: flex;
	flex-wrap: wrap;
	margin-left: -1.5rem;
	margin-right: -1.5rem;
}

.bg-img {
	background-position: 50% 50%;
	background-repeat: no-repeat;
	background-size: cover;
}

.sr-only {
	position: absolute;
	width: 1px;
	height: 1px;
	padding: 0;
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	white-space: nowrap;
	border: 0;
}

/* buttons */
.btn {
	white-space: normal;
}
.btn .fa {
	margin-left: 5px;
}
/* header */
.header {
	display: flex;
	flex-direction: column;
	position: fixed;
	top: 0;
	left: 0;
	transform: translateX(calc(-100% - 0.5rem));
	gap: 5rem;
}
@media (min-width: 993px) {
	.header {
		position: relative;
	}
}
.header__logo {
	display: flex;
	align-items: flex-end;
	font-size: 1rem;
	color: #9197b3;
}
.header__logo-link {
	display: flex;
}
.header__version {
	padding: 1rem 0.4rem;
}
.header__menu {
	margin: 0;
	padding: 0;
	list-style: none;
	display: flex;
	flex-direction: column;
	gap: 1.7rem;
	color: #9197b3;
}
.header__menu-item--active .header__menu-link {
	background: #5932ea;
	color: #fff;
	border-color: #5932ea;
}
.header__menu-item--active .header__menu-link:hover {
	border-color: #5932ea;
}
.header__menu-item--active .header__menu-icon path {
	stroke: currentColor;
}
.header__menu-item--active .header__menu-icon path:not(:nth-child(2)) {
	fill: currentColor;
	stroke-width: 0;
}
.header__menu-item:not(:first-child) .header__menu-link::after {
	content: "";
	height: 0.7rem;
	width: 0.7rem;
	transform: rotate(45deg);
	border-top: 2px solid;
	border-right: 2px solid;
	margin-left: auto;
}
.header__menu-link {
	padding: 1.1rem 1.4rem 1.1rem 1.1rem;
	border-radius: 0.8rem;
	display: flex;
	align-items: center;
	gap: 1.2rem;
	border: 1px solid transparent;
}
.header__menu-link:hover {
	border: 1px solid;
	text-decoration: none;
}
.header__menu-icon {
	flex-shrink: 0;
}
@media (max-width: 991px) {
	.header .navbar-holder {
		position: fixed;
		background: #fff;
		height: 100vh;
		height: calc(var(--vh, 1vh) * 100);
		width: 100%;
		padding: 20rem 0 6.4rem;
		font-size: 2.2rem;
		color: #fff;
		display: flex;
		left: 0;
		bottom: 0;
		transform: translateX(-110%);
		transition: all 0.3s ease;
		z-index: 10;
		overflow: hidden;
	}
}
@media (max-width: 991px) and (max-width: 767px) {
	.header .navbar-holder {
		align-items: flex-start;
		overflow-y: auto;
		height: calc(100vh - 5rem);
		height: calc(var(--vh, 1vh) * 100 - 5rem);
	}
}
@media (max-width: 991px) {
	.menu-opened .header .navbar-holder {
		transform: none;
	}
}

/* footer */
.footer {
	margin-top: auto;
	margin-left: auto;
}
@media (min-width: 993px) {
	.footer {
		margin: 0;
	}
}
.footer__account-info {
	display: flex;
	align-items: center;
	gap: 1.2rem;
}
@media (min-width: 993px) {
	.footer__account-info {
		align-items: unset;
	}
}
.footer__account-info-image {
	border-radius: 50%;
}
.footer__account-info-text {
	font-size: 1.2rem;
}
.footer__account-info-title {
	margin: 0;
}
.footer__account-info-position {
	color: #757575;
	display: none;
}
@media (min-width: 993px) {
	.footer__account-info-position {
		display: block;
	}
}

.sidebar {
	background: #fff;
	padding: 1rem;
	display: flex;
	width: 100%;
}
@media (min-width: 993px) {
	.sidebar {
		padding: 3.6rem 2.8rem 7.6rem;
		flex-direction: column;
		width: clamp(25rem, 25%, 30.6rem);
	}
}
.sidebar__burger {
	display: none;
}
@media (max-width: 991px) {
	.sidebar__burger {
		position: relative;
		width: 3rem;
		height: 3rem;
		border: 0.3rem solid transparent;
		display: block;
		font-size: 0;
		line-height: 0;
	}
}
@media (max-width: 991px) {
	.sidebar__burger span {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 100%;
		transform: translate(-50%, -50%);
		border-bottom: 0.1rem solid;
		height: 0;
		transition: all 0.3s;
	}
	.sidebar__burger span:nth-child(1) {
		margin-top: -1rem;
	}
	.sidebar__burger span:nth-child(3) {
		margin-top: 1rem;
	}
	.menu-opened .sidebar__burger span:nth-child(1) {
		margin-top: 0;
		transform: translate(-50%, -50%) rotate(45deg);
	}
	.menu-opened .sidebar__burger span:nth-child(2) {
		opacity: 0;
		visibility: hidden;
	}
	.menu-opened .sidebar__burger span:nth-child(3) {
		margin-top: 0;
		transform: translate(-50%, -50%) rotate(-45deg);
	}
}